import React, { Component } from 'react';
import { Row, Col, Button } from 'reactstrap';
// import PropTypes from 'prop-types';
import MediaQuery from 'react-responsive';

import Image from 'components/Image';
import { getImage } from 'constants/images';

import SegmentHeader from '../SegmentHeader'

import styles from './DigitalMarketing.module.scss';

const why1 = getImage('landing.why_1');
const why2 = getImage('landing.why_2');
const why3 = getImage('landing.why_3');

const greenFill = getImage('landing.green_fill');
const redFill = getImage('landing.red_fill');
const twiggy = getImage('landing.twiggy');
const octagon1 = getImage('landing.octagon_1');
const octagon2 = getImage('landing.octagon_2');

const data = [{
  img: why1,
  title: 'People ❤️ video',
  desc: 'They say a picture is worth a thousand words — but video is worth so much more. Getting your point across quickly is priceless, but animated videos do just that while driving demand for your brand and turning clicks into sales.',
  fill: greenFill,
}, {
  img: why2,
  title: 'Watching > reading',
  desc: 'Why? Because voice and video have no equal when it comes to capturing attention. Every day, people are becoming less eager to read when they can receive that same information via audio and video. Additionally, the mobility of digital native generations makes them more apt to digest information on the go.',
  fill: redFill,
}, {
  img: why3,
  title: 'Proof in the pudding',
  desc: 'Video makes up over 80% of the internet’s content. People reshare video 10x more than links to text or static images. However, animated explainer videos cost up to 300% less than live action videos, but are just as effective.',
  fill: greenFill,
}]

class DigitalMarketing extends Component {
  renderData(d, index) {
    const imgJSX = (
      <div className={styles.imgContainer}>
        <Image {...d.img} />
      </div>
    )

    const descJSX = (
      <div className={styles.descContainer}>
        <h3>{d.title}</h3>
        <hr />
        <p>{d.desc}</p>
        <div className={styles.wavImg}>
          <Image {...(index % 2 ? greenFill : redFill)} />
        </div>
      </div>
    )

    return (
      <Col className={styles.data} key={`digital-marketing-${index}`} xs={12}>
        <Row noGutters>
          <Col className="d-flex align-items-center justify-content-center" xs={12} sm={index % 2 ? 6 : 6}>
            <MediaQuery minWidth={576}>
              {index % 2 ? descJSX : imgJSX}
            </MediaQuery>
            <MediaQuery maxWidth={575}>
              {imgJSX}
            </MediaQuery>
          </Col>
          <Col className="d-flex align-items-center justify-content-center" xs={12} sm={index % 2 ? 6 : 6}>
            <MediaQuery minWidth={576}>
              {index % 2 ? imgJSX : descJSX}
            </MediaQuery>
            <MediaQuery maxWidth={575}>
              {descJSX}
            </MediaQuery>
          </Col>
        </Row>
      </Col>
    )
  }

  render() {
    return (
      <div id="highlights" className={styles.digitalMarketing}>
        <div className={styles.twiggy}>
          <Image {...twiggy} />
        </div>
        <div className={styles.octagon1}>
          <Image {...octagon1} />
        </div>
        <div className={styles.octagon2}>
          <Image {...octagon2} />
        </div>
        <div className="main-margin">
          <SegmentHeader
            title="WHY ANIMATED VIDEOS"
            subTitle="Animated Videos"
          />
          <Row noGutters>
            {data.map((d, index) => this.renderData(d, index))}
          </Row>
          <p className={[styles.tagline, 'main-margin'].join(' ')}>Want to get in on the action? We’re ready when you are — <Button
            color="info"
            onClick={() => document.querySelector('#enquiry-form').scrollIntoView({ behavior: 'smooth' })}
          // onClick={() => {
          //   try {
          //     window.FB.CustomerChat.showDialog();
          //   } catch(error) {
          //     //eslint-disable-next-line
          //     console.log(error);
          //   }
          // }}
          ><span>Contact us</span></Button>.</p>
        </div>
      </div>
    );
  }
}

DigitalMarketing.defaultProps = {
};

DigitalMarketing.propTypes = {
};

export default DigitalMarketing;
